/* Global reset & base ------------------------------------------------------ */
* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: system-ui,-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Inter,"Helvetica Neue",Arial,sans-serif;
  background:#000;
  color:#eee;
}

body {
  -webkit-font-smoothing: antialiased;
  font-size:16px;
  line-height:1.4;
}

/* Scrollbar (mobile minimal) */
body::-webkit-scrollbar {
  width:8px;
}
body::-webkit-scrollbar-track {
  background:#0a0a0a;
}
body::-webkit-scrollbar-thumb {
  background:#222;
  border-radius:4px;
}

/* Utility text / spacing --------------------------------------------------- */
h1,h2,h3,h4 {
  margin:0 0 .75rem;
  font-weight:700;
  line-height:1.15;
}

p {
  margin:.4rem 0;
}

strong { font-weight:600; }

ul {
  margin:.5rem 0 0;
  padding-left:1.1rem;
}

li { margin:.25rem 0; }

/* Container & Header ------------------------------------------------------- */
.app-container {
  max-width:760px;
  margin:0 auto;
  padding:1.25rem 1.25rem 4rem;
  position:relative;
  min-height:100%;
}

.app-title {
  font-size: clamp(1.9rem, 6vw, 3.2rem);
  text-align:left;
  letter-spacing:.5px;
  margin:0 0 1rem;
  font-weight:800;
}

/* Background watermark logo (behind content) */
.app-container:before {
  content:"";
  position:fixed;
  inset:0;
  background:
    radial-gradient(circle at 50% 35%, rgba(255,255,255,0.08), transparent 60%),
    url("https://blapoker.com/wp-content/uploads/logo_rev.png") center 150px/540px auto no-repeat;
  opacity:.20;
  pointer-events:none;
  mix-blend-mode:normal;
  filter:brightness(.85);
}

/* Auth Card --------------------------------------------------------------- */
.auth-card {
  background:#111;
  border:1px solid #222;
  border-radius:18px;
  padding:1.5rem 1.4rem 1.75rem;
  box-shadow:0 4px 18px -6px rgba(0,0,0,0.7), 0 0 0 1px #1a1a1a inset;
  backdrop-filter:saturate(140%) blur(4px);
  margin-bottom:2rem;
}

.text-input {
  width:100%;
  padding:.75rem .9rem;
  margin:.55rem 0;
  border:1px solid #2a2a2a;
  border-radius:10px;
  background:#090909;
  color:#eee;
  font-size:.95rem;
  transition:border-color .18s, background .18s;
}

.text-input:focus {
  outline:none;
  border-color:#3d8bfd;
  background:#0e0e0e;
}

.toggle-link {
  margin-top:.9rem;
  color:#7ab8ff;
  font-size:.85rem;
  cursor:pointer;
  user-select:none;
}

.toggle-link:hover { text-decoration:underline; }

/* Buttons ----------------------------------------------------------------- */
button {
  font-family:inherit;
  cursor:pointer;
  font-weight:600;
  letter-spacing:.3px;
}

.primary-btn,
.logout-btn,
.small-btn,
.secondary-btn,
.redeem-btn {
  border:none;
  border-radius:12px;
  padding:.85rem 1.2rem;
  font-size:.95rem;
  display:inline-flex;
  align-items:center;
  justify-content:center;
  gap:.5ch;
  transition:background .22s, transform .15s;
  position:relative;
}

.primary-btn.full { width:100%; }

.primary-btn {
  background:#0f7a2f;
  color:#fff;
  box-shadow:0 4px 14px -4px rgba(0,0,0,.65);
}

.primary-btn:hover { background:#12933a; }
.primary-btn:active { transform:translateY(2px); }

.primary-btn.btn-disabled {
  opacity:.45;
  cursor:not-allowed;
  background:#1c482e;
}

.logout-btn {
  background:#c93333;
  color:#fff;
  margin:0 0 1.4rem;
  padding:.7rem 1.15rem;
  font-size:.9rem;
  border-radius:10px;
}

.logout-btn:hover { background:#e54444; }
.logout-btn:active { transform:translateY(2px); }

.secondary-btn {
  background:#2a2a2a;
  color:#ddd;
  padding:.8rem 1.3rem;
  min-width:130px;
}

.secondary-btn:hover { background:#3a3a3a; }
.secondary-btn:active { transform:translateY(2px); }

.small-btn {
  background:#0f7a2f;
  color:#fff;
  font-size:.8rem;
  padding:.55rem .9rem;
  border-radius:10px;
  margin-right:.5rem;
}

.small-btn.green { background:#128735; }
.small-btn.green:hover { background:#169f40; }

.redeem-btn {
  background:#1480e8;
  color:#fff;
  font-size:.7rem;
  padding:.4rem .7rem;
  margin-left:.55rem;
  border-radius:8px;
}

.redeem-btn:hover { background:#2294ff; }

/* Leader Banner ----------------------------------------------------------- */
.leader-banner {
  background:#181818;
  border:1px solid #262626;
  color:#f1d08a;
  padding:.7rem 1rem;
  border-radius:10px;
  margin:-.2rem 0 1.6rem;
  font-size:.85rem;
  text-align:center;
  box-shadow:0 2px 6px -3px rgba(0,0,0,.6);
  display:flex;
  justify-content:center;
  align-items:center;
  gap:.6ch;
  line-height:1.3;
}

/* Panels ------------------------------------------------------------------ */
.panel {
  background:#111;
  border:1px solid #222;
  border-radius:18px;
  padding:1.3rem 1.25rem 1.4rem;
  margin-bottom:1.9rem;
  box-shadow:0 4px 18px -8px rgba(0,0,0,0.75), 0 0 0 1px #1a1a1a inset;
  position:relative;
}

.panel-title {
  font-size:1.5rem;
  margin:.2rem 0 1rem;
  font-weight:700;
}

.logged-in {
  margin:.2rem 0 .6rem;
  font-size:.95rem;
  opacity:.9;
}

.hint {
  margin-top:.9rem;
  font-size:.78rem;
  color:#aaa;
  letter-spacing:.2px;
}

.next-msg {
  margin-top:.6rem;
  font-size:.72rem;
  color:#f1d08a;
  font-weight:500;
}

/* Coupon list ------------------------------------------------------------- */
.coupon-list {
  list-style:none;
  padding:0;
  margin:1.1rem 0 0;
  font-size:.88rem;
}

.coupon-list li {
  background:#181818;
  border:1px solid #242424;
  padding:.55rem .75rem;
  border-radius:10px;
  margin:.4rem 0;
  display:flex;
  flex-wrap:wrap;
  align-items:center;
  gap:.6ch;
  line-height:1.15;
}

.coupon-list.compact li {
  padding:.4rem .6rem;
  font-size:.76rem;
}

.coupon-active { color:#53d96f; font-weight:500; }
.coupon-redeemed { color:#ff6f6f; font-weight:500; }

/* Admin specifics --------------------------------------------------------- */
.admin-user-box {
  background:#151515;
  border:1px solid #262626;
  border-radius:14px;
  padding:.9rem .9rem 1rem;
  margin:.85rem 0 1.2rem;
  font-size:.82rem;
}

.admin-user-line {
  margin:.1rem 0 .4rem;
  font-size:.85rem;
}

.coupon-summary {
  margin:0 0 .6rem;
  font-size:.7rem;
  color:#aaa;
  letter-spacing:.3px;
}

.admin-actions {
  display:flex;
  flex-wrap:wrap;
  gap:.5rem;
  margin-bottom:.5rem;
}

/* QR Modal ---------------------------------------------------------------- */
.qr-modal {
  position:fixed;
  inset:0;
  background:rgba(0,0,0,.76);
  backdrop-filter:blur(6px) saturate(160%);
  display:flex;
  align-items:flex-start;
  justify-content:center;
  padding:clamp(1rem, 3vh, 2rem) 1rem 3rem;
  z-index:500;
  overflow-y:auto;
}

.qr-box {
  width:100%;
  max-width:620px;
  background:#101010;
  border:1px solid #262626;
  border-radius:34px;
  padding:2.4rem 1.9rem 2.3rem;
  box-shadow:
      0 25px 55px -18px rgba(0,0,0,.85),
      0 5px 18px -6px rgba(0,0,0,.55),
      0 0 0 1px #1d1d1d inset;
  position:relative;
}

.qr-title {
  font-size:clamp(1.55rem, 4.6vw, 2.3rem);
  margin:0 0 .65rem;
  text-align:center;
  letter-spacing:.6px;
}

.qr-sub {
  margin:.2rem 0 1.4rem;
  font-size:.85rem;
  color:#bbb;
  text-align:center;
  line-height:1.35;
}

.video-frame {
  position:relative;
  width:100%;
  aspect-ratio:1/1;
  background:#050505;
  border:2px solid #2a2a2a;
  border-radius:22px;
  overflow:hidden;
  display:flex;
  align-items:center;
  justify-content:center;
  margin:0 auto 1.2rem;
  max-width:420px;
}

.qr-video {
  width:100%;
  height:100%;
  object-fit:cover;
  display:block;
  filter:brightness(.97) contrast(1.03) saturate(1.15);
}

.video-frame:before {
  content:"";
  position:absolute;
  inset:0;
  border:6px solid #d32f2f;
  border-radius:22px;
  pointer-events:none;
  mix-blend-mode:normal;
  opacity:.85;
}

.center-msg {
  width:100%;
  text-align:center;
  font-size:.95rem;
  font-weight:500;
  letter-spacing:.4px;
  color:#cfcfcf;
  padding:1rem;
}

.center-msg.error {
  color:#ff7676;
  font-weight:600;
}

.qr-status {
  text-align:center;
  margin:.2rem 0 1.15rem;
  font-size:.75rem;
  letter-spacing:.4px;
  color:#bbb;
  min-height:1.2rem;
}

.retry-btn {
  margin-top:.9rem;
  background:#2e2e2e;
  color:#ddd;
  font-size:.75rem;
  padding:.55rem 1.05rem;
  border:none;
  border-radius:10px;
  font-weight:600;
  cursor:pointer;
  transition:background .18s;
}

.retry-btn:hover { background:#3a3a3a; }

.qr-actions {
  display:flex;
  justify-content:center;
  gap:.9rem;
  flex-wrap:wrap;
}

.qr-actions .secondary-btn {
  min-width:120px;
  font-size:.85rem;
}

/* Buttons inside modal adjustments */
.qr-box button.primary-btn {
  font-size:.9rem;
}

/* Accessibility focus ----------------------------------------------------- */
button:focus, .text-input:focus, .retry-btn:focus {
  outline:2px solid #3d8bfd;
  outline-offset:2px;
}

/* Animations --------------------------------------------------------------- */
.qr-modal {
  animation:fadeIn .35s ease both;
}

.qr-box {
  animation:scaleIn .4s cubic-bezier(.22,1.15,.55,1.01) both;
}

@keyframes fadeIn {
  from { opacity:0; }
  to { opacity:1; }
}

@keyframes scaleIn {
  from {
    opacity:0;
    transform:translateY(25px) scale(.94);
  }
  to {
    opacity:1;
    transform:translateY(0) scale(1);
  }
}

/* Responsive Tweaks -------------------------------------------------------- */
@media (max-width:640px) {
  .panel,
  .auth-card {
    padding:1.15rem 1.05rem 1.3rem;
    border-radius:16px;
  }
  .coupon-list li {
    font-size:.8rem;
    padding:.5rem .65rem;
  }
  .qr-box {
    padding:1.8rem 1.3rem 1.9rem;
    border-radius:30px;
  }
  .video-frame {
    border-radius:20px;
  }
  .video-frame:before {
    border-width:5px;
    border-radius:20px;
  }
  .primary-btn,
  .logout-btn {
    border-radius:10px;
  }
  .app-container:before {
    background:
      radial-gradient(circle at 50% 32%, rgba(255,255,255,0.07), transparent 60%),
      url("https://blapoker.com/wp-content/uploads/logo_rev.png") center 180px/620px auto no-repeat;
    opacity:.18;
  }
}

/* Reduced Motion ---------------------------------------------------------- */
@media (prefers-reduced-motion: reduce) {
  .qr-modal, .qr-box {
    animation:none;
  }
  * {
    scroll-behavior:auto !important;
  }
}

/* Misc -------------------------------------------------------------------- */
.full {
  width:100%;
}

.btn-disabled:hover {
  background:#1c482e;
}

button:disabled {
  opacity:.5;
  cursor:not-allowed;
}

